___CSS_LOADER_EXPORT___.push([module.id, `:root{
  --ink:#1b1026;
  --butter:#ffd86b;
  --lav:#c7b7ff;
  --cocoa:#5e4633;
  --paper:#fffaf2;
  --muted:#8a7f99;
  --ring:rgba(199,183,255,.45);
  --header-h:150px;
}

*{box-sizing:border-box}
html,body{height:100%}
body{
  margin:0;
  color:var(--paper);
  font:16px/1.5 system-ui,-apple-system,Segoe UI,Roboto,Helvetica,Arial,sans-serif;
  background:var(--ink);
}
h1 { font-size: 2rem;}
h2 { font-size: 1.75rem;}
img{max-width:100%;display:block}
ul,ol{margin:0;padding:0;list-style:none}
a{color:inherit;text-decoration:none}

.main-container{
  min-height:100vh;
  display:flex;
  flex-direction:column;
}

.head-nav{
  position:sticky; top:0; z-index:10;
  display:flex; justify-content:space-between; align-items:center;
  height:var(--header-h);
  padding:8px clamp(12px,4vw,32px);
  background:rgba(17,10,25,.7);
  backdrop-filter:blur(6px);
  border-bottom:1px solid rgba(199,183,255,.15);
}
.head-nav .home-link{display:inline-block;padding:10px 0;color:var(--butter);font-weight:700}
.link-nav{display:flex;align-items:center}
.nav-list{display:flex;flex-wrap:wrap;gap:10px}
.nav-li{display:inline-flex}
.nav-btn,.nav-anchor{appearance:none;border:0;background:none;color:var(--lav);padding:8px 12px;border-radius:10px;cursor:pointer}
.nav-btn:hover,.nav-anchor:hover{background:rgba(199,183,255,.10)}
.nav-btn.selected{background:rgba(199,183,255,.15);outline:2px solid var(--ring)}
.nav-btn:focus-visible,.nav-anchor:focus-visible{outline:2px solid var(--butter);outline-offset:2px}

.section-container{
  width:min(1500px,92vw);
  margin:24px auto;
  display:grid;
  grid-template-columns:240px minmax(0,1fr) 360px;
  grid-template-areas: "left main right";
  gap:28px;
  align-items:start;
}

.left-rail{
  grid-area:left;
  position:sticky;
  top:calc(var(--header-h) + 16px);
}
.left-rail .sidebar-card{background:rgba(255,250,242,.06);border:1px solid rgba(199,183,255,.12);border-radius:14px;padding:12px}
.left-rail .latest {margin-bottom:15px;}
.left-rail .latest a,.left-rail .playlists a{color:var(--butter)}
.left-rail .playlists li{margin-top:6px}
.left-rail .playlists a:hover{text-decoration:underline}

.main-body{
  grid-area:main;
  min-width:0;
  display:flex;
  flex-direction:column;
  gap:16px;
  background:rgba(255,250,242,.03);
  border:1px solid rgba(199,183,255,.12);
  border-radius:16px;
  padding:16px;
}

.main-body h1 {color:var(--butter);text-align: center;font-weight: bold;}

.junior-body{
  grid-area:right;
  position:sticky;
  top:calc(var(--header-h) + 16px);
  background:rgba(255,250,242,.03);
  border:1px solid rgba(199,183,255,.12);
  border-radius:16px;
  padding:16px;
}

.intro-message-container{
  background:var(--butter);
  color:#261a0e;
  border:1px solid rgba(199,183,255,.25);
  border-radius:10px;
  padding:14px 16px;
  box-shadow:0 2px 10px rgba(0,0,0,.15);
}
.intro-message-head{margin:0 0 12px;text-align:center;font-weight:800;font-size:1.5em;color:#261a0e}
.intro-message{background:var(--ink);color:var(--paper);border:1px solid rgba(199,183,255,.25);border-radius:8px;padding:12px 14px;line-height:1.55}

.card{background:rgba(255,250,242,.06);border:1px solid rgba(199,183,255,.12);border-radius:14px;padding:12px}
.site-footer{color:var(--muted);border-top:1px solid rgba(199,183,255,.12);text-align:center;padding:18px 16px 28px;margin-top:auto;width:100%;flex-shrink:0}

.home-hero h1{margin:0 0 4px;color:var(--lav)}
.home-cta{display:flex;gap:10px;flex-wrap:wrap;margin-top:12px}
.home-cta button{padding:8px 12px;border-radius:999px;border:1px solid rgba(199,183,255,.2);background:rgba(199,183,255,.08);color:var(--paper);cursor:pointer}
.home-cta button:hover{background:rgba(199,183,255,.15)}

.video-container{
  max-width:760px;
  width:100%;
  margin-inline:auto;
  display:flex;
  flex-direction:column;
  gap:12px;
  border:1px solid rgba(199,183,255,.12);
  background:rgba(255,250,242,.03);
  border-radius:12px;
  padding:12px;
}
.video{display:block;width:100%;height:auto;aspect-ratio:16/9;border:0}
.vid-div{position:relative;width:100%;aspect-ratio:16/9}
.vid-div .video{position:absolute;inset:0;width:100%;height:100%}
.video-meta h2 {color:var(--butter);text-align:center;font-weight:bold;margin-bottom:15px;}

.video-page .vid-page-head{margin:0;color:var(--lav)}
.list-container{margin-top:10px}
.list-head{color:var(--butter);text-align:center;font-weight:bold;}
.list{margin:0;padding:0;display:flex;flex-direction:column;}
.item{margin-block:15px;border-bottom:1px dashed rgba(199,183,255,.15);align-self:center;}
.item:last-child{border-bottom:0}
.nav-anchor{color:var(--butter);font-size:1.1rem;}
.nav-anchor:hover{text-decoration:underline}
.video-desc{margin:.5rem 1.25rem;}

.blog-head {font-weight: bold;text-align: center;margin-bottom: 20px;}
.blog-content h2 {font-weight: bold;color:var(--butter);margin-bottom: 15px;}
.blog-date {text-decoration: underline;}
.blog-content{overflow:auto;}
.blog-content p {margin-bottom: 15px;}

.contact h1{margin:0 0 6px;color:var(--lav)}
.contact-form{display:flex;flex-direction:column;margin-top:10px}
.contact-form input,.contact-form textarea{
  width:100%;padding:10px 12px;border-radius:10px;
  background:rgba(255,250,242,.08);border:1px solid rgba(199,183,255,.18);color:var(--paper)
}
.contact-form input:focus-visible,.contact-form textarea:focus-visible{outline:2px solid var(--butter);outline-offset:2px}
.contact-form button{max-width: 175px;padding:8px 14px;border-radius:10px;border:0;background:linear-gradient(180deg,var(--butter),#ffb94d);color:#261a0e;font-weight:700;cursor:pointer}
.contact-form button:hover{filter:brightness(1.05)}
.form-page{display:flex;flex-direction:column;align-items:center;width:min(900px,95%);margin-inline:auto;padding-bottom:24px}
.form-head{margin:14px 0 6px;text-align:center;font-weight:800;color:var(--lav)}
.form-copy-p{padding-inline:5%;margin-bottom:18px;font-style:italic;color:var(--muted)}
.form-body{min-width: 33vw;background:rgba(255,250,242,.06);border:1px solid rgba(199,183,255,.18);border-radius:16px;padding:14px}
.form-control{margin-bottom:15px;display:block;width:100%;color:var(--paper);background:rgba(255,250,242,.08);border:1.5px solid rgba(199,183,255,.22);border-radius:10px;padding:10px 12px;line-height:1.35;outline:none;transition:box-shadow .15s ease,border-color .15s ease,background .15s ease}
.form-control:focus{border-color:var(--lav);box-shadow:0 0 0 3px rgba(199,183,255,.25);background:rgba(255,250,242,.10)}
.btn{display:inline-block;text-align:center;user-select:none;padding:12px 14px;border-radius:12px;border:0;cursor:pointer}
.btn-lg{font-size:1rem;line-height:1.2}
.btn-block{width:60%;margin:8px auto 0;box-shadow:3px 3px 3px rgba(0,0,0,.25)}
.btn-primary{color:#261a0e;background:linear-gradient(180deg,var(--butter),#ffb94d)}
.btn-primary:hover{filter:brightness(1.03)}

@media (max-width:1100px){
  .section-container{
    grid-template-columns:1fr;
    grid-template-areas:
      "main"
      "right"
      "left";
  }
  .head-nav {display:flex;flex-direction:column;}
  .home-link{font-size:1.5rem;}
  .left-rail,.junior-body{position:static; top:auto}
  .video-container{max-width:100%}
  .form-body{min-width:100%;}
}
